.productContainer{
    width: 80%;
    /* padding: 20px; */
    /* height: auto; */
    margin: 40px auto 10px auto; 
    /* display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)
    );  */
     /* grid-template-columns: minmax(200px, max-content)
                       repeat(auto-fill, 300px) 25%;  */
    /* grid-template-columns: 300px repeat(auto-fill, 300px) 300px; */
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
    padding:20px;
    height:auto;
    /* grid-gap: 1rem; */
}
.popularProduct-container{
  /* width: 250px; */
  height: auto;
  
}
/* product wrapper styling */
.loader-box{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90vw;
  margin: 0 auto;
}
.productWrapper{
  margin: 20px 0;
  height: auto;
  /* width: 247px; */
  
}
.prod-img-wrapper{
  /* width: 245px; */
  height: 450px;
  
}
.prod-img-wrapper img{
    display: block;
    width: 100%;
    height: 100%;
    transition: -webkit-transform 4s;
    transition: transform 4s;
    transition: transform 4s, -webkit-transform 4s;
    cursor: pointer;
    object-fit: cover;
}

.prod-img-wrapper:hover img{
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
    transition: all 0.5s ease-in;
}
/* product wrapper footer */
.productWrapper-footer{
  margin: 10px 0;
  padding: 10px;
  display: flex;
  flex-direction: column;
}
.inner{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.product-details-foot{
  width: 100%;
  /* text-align: left; */
  display: flex;
  justify-content: space-between;
  align-items: center;

}
.product-details-foot .foot-prod-name{
  width: 60%;
  text-align: left;
}
.disc-price h4:nth-child(1){
  text-decoration: line-through;
}
.disc-price h4:nth-child(2){
  color: darkred;
  font-size: 15px;
}
/* product.discoutPrice */
.product-details-foot .foot-prod-price{
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 40%;
  /* text-align: right; */
}
.footer-icons{
  width: 30%;
}
.ratings{
  width: 70%;
  display: flex;
  /* justify-content: center;
  align-items: center; */
}
.star-group{
  flex: 1 1;
}
.reviews{
  flex: 1 1;
  padding: 0;
  margin-top: -5px;
}
.ratings .MuiSvgIcon-root{
  font-size: 20px !important;
}
.footer-icons{
  display: flex;
  justify-content: space-around;
}
.footer-icons .MuiSvgIcon-root{
  font-size: 20px !important;
}
.product-details-foot h4{
  font-size: 18px;
}
span{
  font-size: 14px;
  color: rgba(18, 18, 18, 0.75)
}

/* media query */
@media screen and (max-width:576px) {
    .productContainer {
        height: auto;
        width: 100%;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(100%, 1fr)
        );
        grid-gap: 1rem;
    }

    .popularProduct-container{
      /* width: 250px; */
      height: auto;
    }
    /* product wrapper styling */
  
    .productWrapper{
      height: auto;
    }
    
    .prod-img-wrapper{
      width: 100%;
      height: 65vh;      
    }
    .prod-img-wrapper img{
        display: block;
        width: 100%;
        height: 100%;
        transition: -webkit-transform 4s;
        transition: transform 4s;
        transition: transform 4s, -webkit-transform 4s;
        cursor: pointer;
        object-fit: cover;
    }

   
  
    .inner{
      width: 100%;
     flex-direction: column;
    }
    .ratings{
      width: 100%;
    }
    .footer-icons{
      width: 100%;
    }
    .star-group{
      width: 100%;
    }
    .product-details-foot{
      width: 100%;
      text-align: center;
    }
  }
  
  /* @media screen and (min-width:992px) {
    .productContainer {
      display: grid;
      grid-template-columns: repeat(1, 1fr);
      grid-column-gap: 1em;
    }
  } */
  
.divider{
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    /* padding: 20px 10px; */
}
.divider p{
    display: block;
    flex: 0.7 1;
    padding: 0;
    margin-top: 25px;
    font-size: 26px;
    font-weight: 600;
}
.left-line{
    border:0.1px solid rgba(18, 18, 18, 0.35);
    flex: 1 1;
}
.right-line{
    border:0.1px solid rgba(18, 18, 18, 0.35);
    flex: 1 1;
}


.filter-container{
    display: flex;
    justify-content: space-between;
}
.select-filter{
    width: 60%;
    margin: 0 5px;
    display: flex;
    padding: 5px 5px;
}
.select-filter select{
    margin: 0 10px;
    height:100%;
    width: 100%;
    border: 1px solid #C09621;
    border-radius: 5px;
    padding: 3px 10px; 
}
.filter-right span{
    font-size: medium;
    font-weight: bold;
}
.filter-right{
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30%;
    padding: 10px;
}
.filter{
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 25%;
    padding: 10px;
}
.select{
    width: 60%;
    margin: 0 5px;
    padding: 5px 5px;
}
.filter span{
    font-size: medium;
    font-weight: bold;
}

.filter select{
    margin: 0 10px;
    height:100%;
    width: 80%;
    border: 1px solid #C09621;
    border-radius: 5px;
    padding: 3px 10px; 
}
/* .filter select:active{
    border: none;
} */
.inner-container{
    display: flex;
    flex-direction: column;
}
.inner-container .title{
    width: 100%;
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px auto;
    text-transform: uppercase;
}

@media screen and (max-width: 560px) {
    .inner-container{
        padding: 5px;
        margin: 10px auto;
        height: auto;
    }
    .filter-container{
        flex-direction: column;
        padding: 10px 5px;
        margin: 0 auto;
    }
    .filter-right{
        width: 100%;
        margin: 10px auto;
       
    }
    .filter-right select{
        font-weight: bold;
    }
    .filter{
        width: 100%;
        margin: 10px auto;
    }
    .filter select{
        width: 50%;
        font-weight: bold;
    }
}
